@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css";
@import "https://daneden.github.io/animate.css/animate.min.css";
/*font import - ROBOTO*/
@import url('https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&subset=cyrillic,cyrillic-ext,greek,greek-ext,latin-ext,vietnamese');

/*-----------------------------
|			     Font sizing        |
-------------------------------*/
.font-thin{
	font-weight: 200;
}

.font-light{
	font-weight: 300;
}

.font-bold{
	font-weight: 700;
}

.font-italic{
	font-style: italic;
}

.font-n{
	font-size: 14px;
}

.font-l{
	font-size: 18px;
}

.font-xl{
	font-size: 24px;
}

.font-xxl{
	font-size: 34px;
}

.font-xxxl{
	font-size: 44px;
}

.circle{
  border-radius: 100%;
}

.center{
	text-align: center;
}

.m-center{
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.h-center{
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.justify{
	text-align: justify;
}
/*-----------------------------
|			        Body             |
-------------------------------*/
body,html{
	height: 100%;
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	font-family: 'Roboto', arial;
	font-size: 14px;
}

a{
	text-decoration: none;
}

a:hover{
	color: #d35400;
}

/*-----------------------------
|			        Header           |
-------------------------------*/
#top-navbar{
	border-radius: 0;
	border: 0;
	background-color: white;
	-webkit-box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.07);
	        box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.07);
}

.navbar-default .navbar-nav>li>a{
	font-weight: 500;
	color: #000000;
}

.navbar-default .navbar-nav>li>a:hover,
.navbar-default .navbar-nav>li>a:active
{
	color: #e67e22;
}
/*-----------------------------
|			      Hero-container    |
-------------------------------*/
.hero-container{
	height: 100%;
	top: 0;
	bottom: 0;
	background-image: -webkit-linear-gradient(225deg, #FF5D00 0%, #FFB300 100%);
	background-image: -o-linear-gradient(225deg, #FF5D00 0%, #FFB300 100%);
	background-image: linear-gradient(-135deg, #FF5D00 0%, #FFB300 100%);
}

.hero-circle-1{
	height: 300px;
	width: 300px;
	margin: 130px;
	border-radius: 100%;
	opacity: 0.3;
	position: fixed;
	background: #ffffff;
}

.hero-circle-2{
	height: 500px;
	width: 500px;
	margin: 30px;
	border-radius: 100%;
	opacity: 0.2;
	position: fixed;
	background: #ffffff;
}

.hero-circle-3{
	height: 700px;
	width: 700px;
	margin: -70px;
	border-radius: 100%;
	opacity: 0.1;
	position: fixed;
	background: #ffffff;
}

.hero-image{
	top: 150px;
	opacity: 1;
	position: relative;
}

.hero-text-container{
	margin-top: 150px;
}

.hero-text-h1{
	font-size: 3.7em;
	font-weight: lighter;
	color: #ffffff;
}

.hero-p{
	color: #ffffff;
}

.hero-btn{
	height: 50px;
	width: 150px;
  margin: 10px;
	color: #e67e22;
	font-size: 16px;
}

@media (max-width:768px) {
	body .hero-container{
		height: auto;
		top: 0;
		bottom: 0;
	}
	body .hero-container .hero-text-h1{
		font-size: 3em;
	}
	body .hero-container .hero-btn-section{
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-pack: center;
		    justify-content: center;
	}
	body .hero-container .hero-btn-section .hero-btn{
		margin: 10px;
	}
}


/*-----------------------------
|			      vision-container    |
-------------------------------*/
.vision-container{
	margin-top: 21em;
	position: relative;
	margin-bottom: 20em;
}

.vision-img{
	height: 380px;
}

.vision-tab{
	height: 200px;
	margin-top: -20.5em;
	position: relative;
}

.vision-img-ceo{
	height: 200px;
	border-radius: 100%;
	margin-bottom: 4em;
	/*border: 5px solid #ffffff;*/
	-webkit-box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.3);
	        box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.3);
}

.vision-note{
	width: 60%;
}


/*-------------------------------
|			      project-container    |
-------------------------------*/
.tech-container{
	margin-top: 10em;
	margin-bottom: 10em;
  padding-top: 7em;
  padding-bottom: 7em;
  position: relative;
  color: #333;
  /*background-image: linear-gradient(0deg, #fff 0%, #7f8c8d 100%);*/
}

.tech-icon-container{
  margin-top: 4em;
}

.tech-icon{
	height: 50px;
  float: left;
}


/*-------------------------------
|			      skillbar-container    |
-------------------------------*/
.skillbar-container{
	margin-top: 0em;
	margin-bottom: 10em;
  padding-top: 7em;
  padding-bottom: 7em;
  position: relative;
  color: #fff;
  background: #e67e22;
}

#ed, #ec, #erpd, #sqt, #ma, #uiux {
	margin-top: 10%;
	margin-bottom: 10%;
	width: 100%;
	height: 9px;
}


/*-----------------------------
|			   Board   	       |
-------------------------------*/
.board{
	margin-bottom: 7em;
}

/*-----------------------------
|			  Team     	       |
-------------------------------*/


.card {
    padding-top: 0;
    text-align: center;
    min-height: 340px;
    background-color: #ecf0f1;
}

.card .cardheader {
      background-color: #ffffff;
      background-size: cover;
      height: 80px;
}

.card .card-img-box {
      position: relative;
      top: -50px;
}

.card .card-img-box a {
        text-decoration: none;
		margin: 5px;
}

.card .card-img-box .c-fa {
        color: #999;
        text-decoration: none;
}

.card .card-img-box .c-fa:hover {
        color: #d35400;
        text-decoration: none;
}

.card .card-img-box .blockquote {
        border-left: 5px solid #e67e22;
        text-align: center;
}

.card .card-img-box .quote {
        font-size: 14px;
        font-weight: 300;
        color: #7f8c8d;
        display: inline-block;
        line-height: 1;
}

.card .card-img-box .card-img {
        width: 100px;
        height: 100px;
				border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border: 5px solid rgba(255, 255, 255, 0.5);
}

.card-img-box .join_us_box{
	margin-top: 60px;
}

.card-img-box .join_us{
	top: 500px;
	padding: 10px;
	padding-left: 25px;
	padding-right: 25px;
	background-color: #E67E22;
	border-radius: 40px;
	color: #ffffff;
}

.card-img-box .join_us:hover{
	top: 500px;
	padding: 10px;
	padding-left: 25px;
	padding-right: 25px;
	background-color: #ffffff;
	border-radius: 40px;
	color: #E67E22;
}

/*-----------------------------
|			       reviews     	       |
-------------------------------*/
.review-container{
	margin-top: 10em;
	margin-bottom: 0em;
  padding-top: 7em;
  padding-bottom: 7em;
  position: relative;
  color: #fff;
  background: #e67e22;
}

.reviewer-photo{
	border-radius: 100%;
	height: 150px;
	width: 150px;
}

.sy-pager{
	visibility: hidden;
}

.sy-controls{
	visibility: hidden;
}

.review-content{
	height: 142px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

/*-----------------------------
|			       reviews     	       |
-------------------------------*/

#map{
    height: 400px;
    width: 100%;
}

.map-container{
	margin: 0 0 0 0;
	padding: 0 0 0 0;

}

/*-----------------------------
|			       contact     	       |
-------------------------------*/
.contact-container{
	padding-top: 2em;
	padding-bottom: 2em;
	background-color: #999999;
}

.mail-box{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: end;
	    justify-content: flex-end;
}

.container .row .mail-box-button{
	padding-top: 1em;
	padding-bottom: 1em;
	padding-left: 3em;
	padding-right: 3em;
	background-color: #333333;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	text-decoration: none;
	color: #ffffff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.container .row .mail-box-button:hover{
	padding-top: 1em;
	padding-bottom: 1em;
	padding-left: 3em;
	padding-right: 3em;
	background-color: #ffffff;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	text-decoration: none;
	color: #E67E22;
	font-weight: bold;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

/*-----------------------------
|			     Footer    	       |
-------------------------------*/
.footer{
  position: relative;
  bottom: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #333333;
  z-index: 999;
}

.footer .custom-footer ul li a, .footer-x .custom-footer ul li a{
  color: #bdc3c7;
}

.footer .custom-footer ul li, .footer-x .custom-footer ul li{
  color: #bdc3c7;
  margin: 5px;
}

.footer-list{
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.custom-footer{
  padding: 2em;
}

.copyright{
  color: #bdc3c7;
  font-size: 12px;
  font-weight: 300;
  margin: 5px;
}

.phone{
  font-size: 14px;
  font-weight: 300;
  color: #bdc3c7;
}

.subscribe{
	color: #999999;
	padding: 15px;
	padding-top: 40px; 
	padding-left: 30px;
	padding-bottom: 20px;
	border-bottom: 1px solid #666666;
}

.email_us{
	margin: 15px;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-right: 60px;
	padding-left: 60px;
	background-color: #999999;
	border-radius: 40px;
	text-align: center;
	color: #666666;
	text-decoration: none;
}

.email_us:hover{
	margin: 15px;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-right: 60px;
	padding-left: 60px;
	background-color: #ffffff;
	border-radius: 40px;
	text-align: center;
	color: #666666;
	text-decoration: none;
}


